<template>
  <!-- EmpList.vue -->
  <div class="list row">
    <!-- Í≤ÄÏÉâÏñ¥ ÏãúÏûë -->
    <div class="col-md-8">
      <div class="input-group mb-3">
        <!-- Í≤ÄÏÉâÏñ¥ ÏûÖÎ†• -->
        <!-- üëÄename -> searchEnameÏúºÎ°ú Î∞îÍøà(Ï†ÑÏ≤¥Í≤ÄÏÉâ + ÏÇ¨ÏõêÎ™Ö Í≤ÄÏÉâ Ìï©Ïπú Î≤ÑÏ†Ñ)üëÄ -->
        <input
          type="text"
          class="form-control"
          placeholder="Search by Ename"
          v-model="searchEname"
        />
        <!-- Í≤ÄÏÉâÏñ¥ Î≤ÑÌäº -->
        <div class="input-group-append">
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="
              page = 1;
              retrieveEmp();
            "
          >
            Search
          </button>
        </div>
      </div>
    </div>
    <!-- Í≤ÄÏÉâÏñ¥ ÎÅù -->


    <!-- Ï∂îÍ∞Ä -->
    <!-- <!‚Äî ÌéòÏù¥Ïßï + Ï†ÑÏ≤¥ Î™©Î°ù ÏãúÏûë ‚Äî> -->
    <!-- <!‚Äî ÌéòÏù¥Ïßï ÏñëÏãù ÏãúÏûë ‚Äî> -->
    <div class="col-md-12">
      <!-- <!‚Äî ÏÖÄÎ†âÌä∏ Î∞ïÏä§ : Í∏∞Î≥∏ ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω ‚Äî> -->
      <!-- <!‚Äî change :  üí´handlePageSizeChange($event), 1ÌéòÏù¥ÏßÄÎãπ Í∞úÏàò Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏‚Äî>üí´ -->
      <!-- <!‚Äî           $event : html ÌÉúÍ∑∏Ïùò Í∏∞Î≥∏ Ïù¥Î≤§Ìä∏, Ïù¥ Í∞ùÏ≤¥Î°ú ÌòÑÏû¨ ÏÑ†ÌÉù ÎòêÎäî ÌÅ¥Î¶≠Ìïú htmlÌÉúÍ∑∏(ÏñëÏãù)Î•º Ïïå Ïàò ÏûàÏùå.  -->
                     <!-- event.target (ÌòÑÏû¨ ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÑ†ÌÉùÌïú ÏñëÏãù(Ïó¨Í∏∞ÏÑúÎäî ÏÖÄÎ†âÌä∏ Î∞ïÏä§ÎÇ¥ Î™©Î°ùÏùÑ ÏùòÎØ∏ ) ‚Äî> -->
      <div class="mb-3">
        Items per Page:
        <select v-model="pageSize" @change="handlePageSizeChange($event)">
          <option v-for="size in pageSizes" :key="size" :value="size">
            {{ size }}
          </option>
        </select>
      </div>

      <!-- <!‚Äî b-pagination : üëÄbootstrap-vueÏùò ÌéòÏù¥ÏßÄ Î≤àÌò∏ Ïª®Ìä∏Î°§ _Ïô∏Î∂ÄÎùºÏù¥Î∏åÎü¨Î¶¨ ÏÑ§ÏπòÌï¥Ïïº b-pa.. ÌÉúÍ∑∏ ÏÇ¨Ïö©Í∞ÄÎä• ‚Äî> -->
      <!-- Îß§Í∞úÎ≥ÄÏàò page, count Îßå ÎÑòÍ∏∞Î©¥ ÏûêÎèôÏúºÎ°ú ÎèôÏûë! Îß§Ïö∞ Ìé∏Î¶¨ (Ï¥ù ÌéòÏù¥ÏßÄ, ÌéòÏù¥ÏßÄÎãπ ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò) -->
      <!-- <!‚Äî total-rows : Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í∞úÏàò ‚Äî> -->
      <!-- <!‚Äî per-page : 1ÌéòÏù¥ÏßÄ Îãπ Í∞úÏàò ‚Äî> -->
      <!-- prev-text="ÌôîÎ©¥Ïóê Î≥¥Ïùº Í∏ÄÏûê"  : Ï†Ñ ÌéòÏù¥ÏßÄ Ïù¥Îèô-->
      <!-- next-text="ÌôîÎ©¥Ïóê Î≥¥Ïùº Í∏ÄÏûê"  : Îã§Ïùå ÌéòÏù¥ÏßÄ Ïù¥Îèô-->
      <!-- <!‚Äî change : üî±handlePageChange(), ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(Ïû¨Ï°∞Ìöå Ìï®Ïàò) ‚Äî> -->
      <b-pagination
        v-model="page"
        :total-rows="count"
        :per-page="pageSize"
        prev-text="Prev"
        next-text="Next"
        @change="handlePageChange"
      ></b-pagination>
    </div>
    <!-- <!‚Äî ÌéòÏù¥Ïßï ÏñëÏãù ÎÅù ‚Äî> -->


    <!-- Ï†ÑÏ≤¥ Î™©Î°ù Ï°∞Ìöå ÏãúÏûë -->
    <div class="col-md-6">
      <h4>Emp List</h4>
      <ul class="list-group">
        <li
          class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(data, index) in emp"
          :key="index"
          @click="setActiveEmp(data, index)"
        >
          {{ data.ename }}
        </li>
      </ul>

      <!-- ÏÇ≠Ï†ú Î≤ÑÌäº ÏãúÏûë-->
      <button class="m-3 btn btn-sm btn-danger" @click="removeAllEmp">
        Remove All
      </button>
      <!-- ÏÇ≠Ï†ú Î≤ÑÌäº ÎÅù-->
    </div>
    <!-- Ï†ÑÏ≤¥ Î™©Î°ù Ï°∞Ìöå ÎÅù -->
    <!-- <!‚Äî ÌéòÏù¥Ïßï + Ï†ÑÏ≤¥ Î™©Î°ù ÎÅù ‚Äî> -->


    <!-- Í∞ÑÎûµ ÏÉÅÏÑ∏ Î™©Î°ù ÏãúÏûë -->
    <div class="col-md-6">
      <div v-if="currentEmp">
        <h4>Emp</h4>
        <div>
          <label><strong>Ename:</strong></label> {{ currentEmp.ename }}
        </div>
        <div>
          <label><strong>job:</strong></label> {{ currentEmp.job }}
        </div>
        <div>
          <label><strong>manager:</strong></label> {{ currentEmp.manager }}
        </div>
        <div>
          <label><strong>hiredate:</strong></label> {{ currentEmp.hiredate }}
        </div>
        <div>
          <label><strong>salary:</strong></label> {{ currentEmp.salary }}
        </div>
        <div>
          <label><strong>commission:</strong></label>
          {{ currentEmp.commission }}
        </div>
        <div>
          <label><strong>eno:</strong></label> {{ currentEmp.eno }}
        </div>
        <!-- ÏÇ¨ÏõêÏ†ïÎ≥¥ ÏàòÏ†ï Î≤ÑÌäº -->
        <router-link :to="'/emp/' + currentEmp.eno"
          ><span class="badge bg-success">Edit</span></router-link
        >
      </div>
      <div v-else>
        <br />
        <p>Please click on a Emp...</p>
      </div>
    </div>
    <!-- Í∞ÑÎûµ ÏÉÅÏÑ∏ Î™©Î°ù ÎÅù -->
  </div>
</template>

<script>
// axios Í≥µÌÜµÌï®Ïàò import
import EmpDataService from "../../services/EmpDataService";

export default {
  // Î≥ÄÏàò Ï†ïÏùòÌïòÎäî Í≥≥ : data(), Ï¥àÍ∏∞Ìôî
  data() {
    return {
      emp: [],
      currentEmp: null,
      currentIndex: -1,
      // ename: "", ÏÜåÏä§ Î≥ÄÍ≤Ω-> searchEname: "",
      searchEname: "",  // Í≤ÄÏÉâÏñ¥. ename -> searchEnameÏúºÎ°ú Î≥ÄÍ≤Ω -> Í∏∞Ï°¥Ïùò searchEname Ìï®Ïàò(likeÍ≤ÄÏÉâ) ÏïàÏì∞ÎãàÍπå ÎßâÏïÑÏïº Ìï®

      // ÌéòÏù¥ÏßïÏùÑ ÏúÑÌïú Î≥ÄÏàò Ï†ïÏùò(ÏµúÏ¥àÏÑ∏ÌåÖ) 4Í∞ÄÏßÄ
      page: 1,      // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
      count: 0,     // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò
      pageSize: 3,  // Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï§Ñ Í∞úÏàòÎ•º Í≤∞Ï†ïÌïòÎäî Î≥ÄÏàò
      pageSizes: [3, 6, 9]  // ÏÖÄÎ†âÌä∏Î∞ïÏä§Ïóê ÎÑ£ÏùÑ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞. ÏÑ†ÌÉùÏ∞Ω
    };
  },
  // Ìï®Ïàò Ï†ïÏùòÌïòÎäî Í≥≥ : methods:
  methods: {
    // axios , Î™®Îì† Î∂ÄÏÑú Ï†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // getAll(dname, page, size)Î°ú Ìï®Ïàò Î≥ÄÍ≤ΩÎê®
    retrieveEmp() {
      //                                      ÏûêÎèô ÌéòÏù¥ÏßïÏ≤òÎ¶¨Í∞Ä 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎØÄÎ°ú -1Ìï¥ÏÑú 0ÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï§å
      EmpDataService.getAll(this.searchEname, this.page-1, this.pageSize)
        // ÏÑ±Í≥µÌïòÎ©¥ .then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
        .then((response) => {
          // this.emp = response.data;  -> Î≥ÄÍ≤Ω -> const {emp, totalItems} = response.data;
          // let(const){ÏÜçÏÑ±Î™Ö, ÏÜçÏÑ±Î™Ö2} = Îç∞Ïù¥ÌÑ∞Í∞ùÏ≤¥Î∞∞Ïó¥   : Î™®ÎçòÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î¨∏Î≤ï Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
          const {emp, totalItems} = response.data; // springboot ÏóêÏÑú Ï†ÑÏÜ°Îêú Îßµ(map) Ï†ïÎ≥¥
          this.emp = emp;         // Ïä§ÌîÑÎßÅ Î∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú Îç∞Ïù¥ÌÑ∞
          this.count = totalItems;  // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú ÌéòÏù¥ÏßÄÏ†ïÎ≥¥(Ï¥ù Í±¥Ïàò) _Î∞±ÏóîÎìúÏóêÏÑú Î≥¥ÎÇ∏ Ïù¥Î¶ÑÍ≥º ÎòëÍ∞ôÏïÑÏïº Ìï®
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
    },
    // Î™©Î°ùÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑÎïå ÌòÑÏû¨ Î∂ÄÏÑúÍ∞ùÏ≤¥, Ïù∏Îç±Ïä§Î≤àÌò∏Î•º Ï†ÄÏû•ÌïòÎäî Ìï®Ïàò
    setActiveEmp(data, index) {
      this.currentEmp = data;
      this.currentIndex = index;
    },
    // axios, Î™®Îì† Î∂ÄÏÑú Ï†ïÎ≥¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    removeAllEmp() {
      EmpDataService.deleteAll()
        // ÏÑ±Í≥µÌïòÎ©¥ .then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
        .then((response) => {
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
          // Ï†ÑÏ≤¥ Î™©Î°ù Ïû¨Ï°∞Ìöå
          this.retrieveEmp();
          // currentEmp, currentIndex Ï¥àÍ∏∞Ìôî
          this.currentEmp = null;
          this.currentIndex = -1;
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
    },
    // enameÏúºÎ°ú like Í≤ÄÏÉâÌïòÎäî Ìï®Ïàò -> ename -> searchEname Ïù¥Î¶Ñ Î∞îÍæ∏Î©¥ÏÑú searchEname()Ìï®Ïàò ÏÇ≠Ï†ú
     // select box Í∞í Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üí´
    // event : ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÅ¥Î¶≠Ìïú Ïª®Ìä∏Î°§Ïù¥ Ïù¥Î≤§Ìä∏Í∞ùÏ≤¥Î•º ÌÜµÌï¥ Ï†ëÍ∑º,ÌôïÏù∏Í∞ÄÎä•
    handlePageSizeChange(event){
      this.pageSize = event.target.value    // ÏÖÄÎ†âÌä∏Î∞ïÏä§ Í∞í Í∞ÄÏ†∏Ïò§Í∏∞ -> Ìïú ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò Ï†ÄÏû•
      this.page = 1;    // ÌòÑÏû¨ÌéòÏù¥ÏßÄÎ•º Ï≤´ ÌéòÏù¥ÏßÄÎ°ú ÏÑ∏ÌåÖ
      // Ïû¨Ï°∞ÌöåÌï®Ïàò Ìò∏Ï∂ú
      this.retrieveEmp();
    },
    // ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üî±
    handlePageChange(value){
      // Îß§Í∞úÎ≥ÄÏàòÍ∞íÏúºÎ°ú ÌòÑÏû¨ ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
      this.page = value;
      // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
      this.retrieveEmp();
    },
  },
  // ÌôîÎ©¥Ïù¥ Îú®ÏûêÎßàÏûê Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(ÎùºÏù¥ÌîÑ ÏÇ¨Ïù¥ÌÅ¥ Ìï®Ïàò) : mounted(), created()
  mounted() {
    this.retrieveEmp(); // ÌôîÎ©¥ Î°úÎî©Ïãú Ï†ÑÏ≤¥ Ï°∞ÌöåÌï®Ïàò Ïã§Ìñâ
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
