import http from "../http-common";

class EmpDataService {
    // Î™®Îì† ÏÇ¨ÏõêÏ†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò + ÏÇ¨ÏõêÎ™Ö Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò 
    // ename : ÏÇ¨ÏõêÎ™Ö 
    // page : ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏ 
    // size : Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò
    getAll(ename, page, size) {
        //   alert("ÌôïÏù∏Ïö©2")
    
            // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/dept")
            // 1) Ï†ÑÏ≤¥ Ï°∞Ìöå ÏôÄ
            // 2) ÏÇ¨ÏõêÎ™Ö Ï°∞ÌöåÎ•º Í∞ôÏù¥ ÌïòÎäî Ìï®Ïàò getAll
            return http.get(`/emp?ename=${ename}&page=${page}&size=${size}`); 
        }

    // ÏÇ¨ÏõêÎ≤àÌò∏Î°ú Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/emp/{eno}"), @PathVariable
    get(eno) {
        return http.get(`/emp/${eno}`)
    }

    // ÏÇ¨ÏõêÏ†ïÎ≥¥ ÏÉùÏÑ±(insert) ÏöîÏ≤≠ Ìï®Ïàò
    // post Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PostMapping("/api/emp"), @RequestBody
    create(data) {
        return http.post("/emp", data);
    }

    // ÏÇ¨ÏõêÏ†ïÎ≥¥ ÏàòÏ†ï(update) ÏöîÏ≤≠ Ìï®Ïàò
    // put Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PutMapping("/api/emp/{eno}"), @RequestBody
    update(eno, data) {
        return http.put(`/emp/${eno}`, data);
    }

    // ÏÇ¨ÏõêÏ†ïÎ≥¥ ÏÇ≠Ï†ú(delete) ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/emp/deletion/{eno}")
    //                        , @PathVariable  
    delete(eno) {
        return http.delete(`/emp/deletion/${eno}`);
    }

    // ÏÇ¨ÏõêÏ†ïÎ≥¥ Ï†ÑÏ≤¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/emp/all")
    deleteAll() {
        return http.delete("/emp/all")
    }

    // // ÏÇ¨ÏõêÎ™Ö Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò
    // // ÏøºÎ¶¨Ïä§Ìä∏ÎßÅ Î∞©Ïãù url : ?Î≥ÄÏàòÎ™Ö=Í∞í&Î≥ÄÏàòÎ™Ö2=Í∞í2....
    // // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/emp") , @RequestParam
    // findByEname(ename) {
    //     return http.get(`/emp?ename=${ename}`);
    // }

}

export default new EmpDataService();